/*--------------------------------------------------------------
14.0 - Infinite Scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none; }

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before */
.infinity-end.neverending .site-footer {
  display: block; }

/*--------------------------------------------------------------
15.0 - Media Queries
----------------------------------------------------------------
15.1 - Tablet (<35ems)
--------------------------------------------------------------*/
@media only screen and (min-width: 35em) {
  .site-header,
  .site-breadcrumbs,
  .site-main,
  .site-footer,
  .sidebar-style-constant .site-header,
  .sidebar-style-constant .site-main,
  .sidebar-style-constant .site-footer {
    width: 82.5%; }
  .site-breadcrumbs-container {
    width: 100%; }
  .sidebar-link {
    position: fixed;
    z-index: 1;
    top: 2.5%;
    width: 2em;
    height: 2em; }
    .sidebar-link.left {
      left: 2.5%; }
    .sidebar-link.right {
      right: 2.5%; }
    .admin-bar .sidebar-link {
      top: calc(2.5% + 32px); } }
    @media only screen and (min-width: 35em) and (max-width: 782px) {
      .admin-bar .sidebar-link {
        top: calc(2.5% + 46px); } }

@media only screen and (min-width: 35em) {
    .sidebar-link svg,
    .sidebar-link
img {
      width: 2em;
      height: 2em; }
  .sidebar {
    width: 55%; }
  .social-links {
    margin: 1.5% auto; }
  .social-link svg,
  .social-link img {
    width: 2.5em;
    height: 2.5em; }
  /* The CSS below causes the absolutely positioned dropdowns
   to display relative to the menu items that they are children of. */
  .menu li {
    position: relative; }
  .no-touch .menu ul li:hover > ul,
  .no-touch .menu ul ul:hover,
  .menu ul li.open > ul {
    width: auto; }
  .menu ul li.open ul {
    position: absolute;
    float: left; }
  .header-menu ul ul {
    right: auto;
    left: auto; }
  .menu ul ul {
    padding: 0.25em 0.5em; }
  .menu ul ul a {
    width: 10em; }
  .horizontal-menu ul ul ul {
    left: -6%; }
  .footer-menu ul ul ul {
    top: auto; }
  .vertical-menu ul ul ul {
    left: -.5em; }
  .gallery-columns-3 .gallery-caption,
  .gallery-columns-4 .gallery-caption {
    display: block;
    /* Show galleries that will fit on tablet-sized screens. */ }
  .comment-form .comment-form-author,
  .comment-form .comment-form-email,
  .comment-form .comment-form-url {
    margin: 0 0.25em;
    flex-basis: 30%; }
  .comment-form .comment-form-author {
    margin-left: 0; }
  .comment-form .comment-form-url {
    margin-right: 0; }
  .form-allowed-tags.visible {
    max-height: 7em; } }

